// LOGOTYPE :: START

.logo {
  &--footer {
    padding-bottom: 25px;
  }

  &__link {
    display: flex;
    @include respond(max-extra-small) {
      justify-content: center;
    }

    &:hover {
      svg {
        fill: #cd084b;
        @include transition(fill);
      }
    }

    svg {
      fill: $c-white;
      @include transition(fill);
    }
  }
}

// LOGOTYPE :: END