// NAV :: START

.nav {
  &--footer {
    .nav__list {
      padding-bottom: 55px;
      @include respond(max-extra-small) {
        padding-bottom: 27px;
      }
    }

    .nav__link {
      display: flex;
      align-items: center;
      min-height: 40px;
      color: #8F8F8F;
      @include respond(max-extra-small) {
        justify-content: center;
      }

      &:hover {
        color: $c-white;
      }
    }
  }

  &__list {}

  &__item {}

  &__link {
    @include transition(color);

    &:hover {
      @include transition(color);
    }
  }
}

// NAV :: END