// COMMON :: START


// address
.address {
  &__row {
    color: #8F8F8F;

    p {
      @include respond(max-extra-small) {
        text-align: center;
      }
    }

    &-0 {
      p {
        min-height: 40px;
        padding-bottom: 15px;
      }
    }

    &-1 {
      padding-bottom: 40px;
      @include respond(max-extra-small) {
        padding-bottom: 27px;
      }
    }

    &-2 {
      display: flex;
      flex-direction: column;
      @include respond(max-extra-small) {
        align-items: center;
      }

      a {
        color: #8F8F8F;
        @include transition(color);

        &:hover {
          color: $c-white;
          @include transition(color);
        }

        &:first-of-type {
          padding-bottom: 10px;
          color: $c-white;

          &:hover {
            color: #8F8F8F;
          }
        }
      }
    }
  }
}
// address :: end


// copyright
.copyright {
  &__text {
    color: #8F8F8F;
    font-size: 16px;
  }
}
// copyright :: end

// COMMON :: END